import React, { useEffect } from "react";
import axios from "axios";
import { useRef } from "react";
import { useState } from "react";
import ReactPaginate from "react-paginate";
import { PAGELIMIT } from "../Constants/Index";
// import ImageToBase64 from "../components/ImageToBase64";
import DateFormater from "../components/DateFormater";

const Client = () => {
  const [visible, setVisible] = useState(false);
  const [fullName, setFullName] = useState("");
  const [clientId, setClientId] = useState("");
  const [businessName, setBusinessName] = useState("");
  const [mobile, setMobile] = useState("");
  const [email, setEmail] = useState("");
  const [city, setCity] = useState("");
  const [state, setState] = useState("");
  const [service, setService] = useState("");
  const [serviceDuration, setServiceDuration] = useState("Monthly");
  const [webURL, setWebURL] = useState("N/A");
  const [instaURL, setInstaURL] = useState("N/A");
  const [amount, setAmount] = useState("");
  const [file, setFile] = useState("");
  const [refer, setRefer] = useState("N/A");
  const [clients, setClients] = useState([]);
  const [originalClients, setOriginalClients] = useState([]);
  const [refered, setRefered] = useState(false);
  const [searchClient, setSearchClient] = useState(false);

  const [pageCount, setPageCount] = useState(1);
  const currentPage = useRef(1);

  const handleSubmit = async (e) => {
    try {
      e.preventDefault();

      // const base64 = await ImageToBase64(profile);
      // if (base64 === "File size over limit") {
      //   return alert("File size over limit");
      // }
      const formData = new FormData();
      formData.append("pic", file);
      formData.append("fullName", fullName);
      formData.append("businessName", businessName);
      formData.append("mobile", mobile);
      formData.append("email", email);
      formData.append("city", city);
      formData.append("state", state);
      formData.append("service", service);
      formData.append("serviceDuration", serviceDuration);
      formData.append("websiteUrl", webURL);
      formData.append("instagramUrl", instaURL);
      formData.append("amount", amount);
      formData.append("refer", refer);
      formData.append("clientId", clientId);

      const res = await axios.post("/addClient", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });
      if (res.data.success) {
        setVisible(false);
        setFullName("");
        setBusinessName("");
        setMobile("");
        setEmail("");
        setCity("");
        setState("");
        setService("");
        setServiceDuration("Monthly");
        setWebURL("N/A");
        setInstaURL("N/A");
        setAmount("");
        setFile("");
        setRefer("N/A");
        console.log("Client Added Successfully");
      }
    } catch (error) {
      console.log(error);
    }
  };
  const getPaginatedData = async () => {
    try {
      const res = await axios.get(
        `/clients?page=${currentPage.current}&limit=${PAGELIMIT}`
      );
      if (res.data.success) {
        const data = res.data.data;
        if (data.results.length === 0) {
          setOriginalClients([]); // Store the original list of users
        } else {
          setPageCount(data.totalPages); // Use total pages from the response
          setClients(data.results);
        }
      }
    } catch (error) {
      console.log(error);
    }
  };
  const handlePageClick = (e) => {
    const selectedPage = e.selected + 1;
    currentPage.current = selectedPage;
    getPaginatedData(selectedPage); // Fetch data for the selected page
  };
  const filterByDuration = (role) => {
    if (role === "all") {
      setClients(originalClients); // Restore the original list of users
    } else {
      const filteredUsers = originalClients.filter(
        (user) => user.role === role
      );
      setClients(filteredUsers); // Filtered users by role
    }
  };
  // SG 456789
  const handleSearch = async () => {
    try {
      const res = await axios.get(`/clients?refer=${refer}`);
      if (res.data.success) {
        setSearchClient(true);
        setRefered(true);
      }else{
        setSearchClient(true);
        setRefered(false);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getPaginatedData();
  }, []);

  return (
    <>
      <header className="flex justify-between mx-10">
        <select className="myCustomBtn px-3 py-1 rounded">
          <option value={"One  Time"}>One Time</option>
          <option value={"Monthly"}>Monthly</option>
          <option value={"Yearly"}>Yearly</option>
        </select>
        <button
          className="myCustomBtn p-3 rounded-md"
          onClick={() => {
            setVisible(true);
          }}
        >
          Add client
        </button>
      </header>

      <div className="w-full">
        {visible ? (
          <>
            <div className="justify-center items-center flex overflow-auto absolute top-0 sm:mx-0 md:mx-20 z-50 outline-none focus:outline-none">
              <div className="relative w-auto my-6 mx-auto max-w-3xl">
                {/*content*/}
                <div className="border-0 rounded-lg shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none">
                  {/*header*/}
                  <div className="flex items-start justify-between p-5 border-b border-solid border-blueGray-200 rounded-t">
                    <h3 className="text-2xl">Add Client</h3>
                    <button
                      className="p-1 ml-auto bg-transparent border-0 text-black float-right text-3xl leading-none outline-none focus:outline-none font-weight-bold"
                      onClick={() => setVisible(false)}
                    >
                      x
                    </button>
                  </div>
                  {/*body*/}
                  <form autoComplete="off">
                    <div className="mt-6">
                      <div className="flex flex-col justify-center dark:bg-black  ">
                        <div className="flex flex-wrap gap-x-10 mx-10 my-1 justify-start">
                          <div className="my-3 ">
                            <div className="text-black font-normal ">Name</div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter Name"
                              value={fullName}
                              onChange={(e) => setFullName(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Business Name
                            </div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter Business name"
                              value={businessName}
                              onChange={(e) => setBusinessName(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Mobile{" "}
                            </div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter Mobile"
                              value={mobile}
                              onChange={(e) => setMobile(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">Email</div>
                            <input
                              type="email"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter Email"
                              value={email}
                              onChange={(e) => setEmail(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">City</div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter City"
                              value={city}
                              onChange={(e) => setCity(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">State</div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter State"
                              value={state}
                              onChange={(e) => setState(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Service
                            </div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter Service"
                              value={service}
                              onChange={(e) => setService(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Service Duration
                            </div>
                            <select
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              onChange={(e) => {
                                setServiceDuration(e.target.value);
                              }}
                            >
                              <option value={"Monthly"}>Monthly</option>
                              <option value={"Yearly"}>Yearly</option>
                              <option value={"One Time"}>One Time</option>
                            </select>
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Website URL
                            </div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter Website URL"
                              value={webURL}
                              onChange={(e) => setWebURL(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Instagram URL
                            </div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter Insta URL"
                              value={instaURL}
                              onChange={(e) => setInstaURL(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Amount
                            </div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter Amount"
                              value={amount}
                              onChange={(e) => setAmount(e.target.value)}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Profile
                            </div>
                            <input
                              type="file"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              onChange={(e) => setFile(e.target.files[0])}
                            />
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Client Id
                            </div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black cursor-not-allowed"
                              placeholder="Enter Client Id"
                              value={clientId}
                              onChange={(e) => setClientId(e.target.value)}
                              disabled
                            />
                            <button
                              className="myCustomBtn px-3 py-1 rounded ms-1"
                              onClick={(e) => {
                                e.preventDefault();
                                e.stopPropagation();
                                const cId = mobile.slice(-6);
                                setClientId(`SG-${cId}`);
                              }}
                              disabled={
                                mobile.length === 0 || mobile.length < 10
                                  ? true
                                  : false
                              }
                            >
                              Generate
                            </button>
                          </div>
                          <div className="my-3 ">
                            <div className="text-black font-normal ">
                              Refer(Optional)
                            </div>
                            <input
                              type="text"
                              className="w-48 h-9 ps-1 bg-white rounded border border-black"
                              placeholder="Enter Refer Client"
                              value={refer}
                              onChange={(e) => setRefer(e.target.value)}
                            />
                            {/* SG 456789 */}
                            {searchClient ? (
                              <span
                                className={`${
                                  refered ? "text-green-500" : "text-red-500"
                                }`}
                              >
                                {refered ? "Client Found" : "Client Not Found"}
                              </span>
                            ) : null}
                            <button
                              className="myCustomBtn px-3 py-1 rounded ms-1"
                              onClick={(e) => {
                                e.preventDefault();
                                e.stopPropagation();
                                handleSearch();
                              }}
                              disabled={refer === "N/A" ? true : false}
                            >
                              Search
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="relative p-6 flex-auto"></div>
                    {/*footer*/}
                    <div className="flex items-center justify-end p-6 border-t border-solid border-blueGray-200 rounded-b">
                      <button
                        className="myCustomBtn  font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                        type="submit"
                        disabled={
                          !fullName ||
                          !businessName ||
                          !mobile ||
                          !email ||
                          !city ||
                          !state ||
                          !service ||
                          !serviceDuration ||
                          !amount ||
                          !file ||
                          !amount ||
                          !clientId
                        }
                        onClick={(e) => {
                          setVisible(false);
                          handleSubmit(e);
                        }}
                      >
                        Add
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
            <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
          </>
        ) : null}
      </div>

      <main className="overflow-x-auto mx-7 mt-6 rounded-lg">
        <table className="text-md text-left bg-white w-1/2 mx-auto">
          <thead className="text-md">
            <tr>
              <th scope="col" className="md:px-6 md:py-3 px-1 py-1">
                Sn.
              </th>
              <th scope="col" className="md:px-6 md:py-3 px-1 py-1">
                Pic
              </th>
              <th scope="col" className="md:px-6 md:py-3 px-1 py-1">
                Name
              </th>
              <th
                scope="col"
                className="md:px-6 md:py-3 px-1 py-1 whitespace-nowrap"
              >
                Client ID
              </th>
              <th scope="col" className="md:px-6 md:py-3 px-1 py-1">
                Service
              </th>
              <th scope="col" className="md:px-6 md:py-3 px-1 py-1">
                Date
              </th>
              <th scope="col" className="md:px-6 md:py-3 px-1 py-1">
                Duration
              </th>
              <th scope="col" className="md:px-6 md:py-3 px-1 py-1">
                Amount
              </th>
            </tr>
          </thead>
          <tbody>
            {clients && clients.length > 0 ? (
              clients.map((user, idx) => (
                <tr className="bg-white border-b" key={idx + 1}>
                  <td className="px-6 py-4">{idx + 1}</td>
                  <td className="px-6 py-4">
                    <div className="h-12 w-12 border-2 border-black overflow-hidden rounded-full">
                      <img src={user.pic} alt="Client Pic"></img>
                    </div>
                  </td>
                  <td className="px-6 py-4">{user.name}</td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    {user.clientId}
                  </td>
                  <td className="px-6 py-4 whitespace-break-spaces">
                    {user.service.length > 20
                      ? user.service.slice(0, 20) + `...`
                      : user.service}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    {DateFormater(user.createdAt)}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    {user.serviceDuration}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">{user.amount}</td>
                </tr>
              ))
            ) : (
              <tr className="bg-white border-b">
                <td className="px-6 py-4 text-center text-red-600" colSpan={8}>
                  No data Found
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </main>
      <ReactPaginate
        breakLabel="..."
        nextLabel="next >"
        onPageChange={handlePageClick}
        pageRangeDisplayed={5}
        pageCount={pageCount}
        previousLabel="< previous"
        renderOnZeroPageCount={null}
        marginPagesDisplayed={2}
        containerClassName="justify-center flex items-center mt-5 whitespace-nowrap"
        pageClassName="px-1 bg-white border-1 border-black rounded w-min mx-1 py-1"
        pageLinkClassName="page-link"
        previousClassName="px-1 bg-white border-1 border-black rounded w-min mx-1 py-1"
        previousLinkClassName="page-link"
        nextClassName="px-1 bg-white border-1 border-black rounded w-min mx-1 py-1 "
        nextLinkClassName="disabled:bg-slate-400"
        activeClassName="px-1 myCustomBtn rounded w-min mx-1 py-1"
      />
    </>
  );
};

export default Client;
