import React from "react";
import { useNavigate } from "react-router-dom";
import back from "../assets/icons/back.png";

function AllotWork() {
  const navigate = useNavigate();
  return (
    <>
      <header className="flex justify-between mx-10">
        <button
          className="myCustomBtn p-3 rounded-md flex"
          onClick={() => {
            navigate("/employee");
          }}
        >
          <img src={back} alt="Back" className="h-5" />
        </button>
      </header>
      <main className="overflow-x-auto mx-7 mt-6 rounded-lg">
        <table className="text-md text-left bg-white w-1/2 mx-auto">
          <thead class="text-md">
            <tr>
              <th scope="col" class="px-6 py-3">
                Pic
              </th>
              <th scope="col" class="px-6 py-3">
                Name
              </th>
              <th scope="col" class="px-6 py-3">
                Service
              </th>
              <th scope="col" class="px-6 py-3">
                Date
              </th>
              <th scope="col" class="px-6 py-3">
                Due date
              </th>
              <th scope="col" class="px-6 py-3">
                Actions
              </th>
            </tr>
          </thead>
          <tbody>
            <tr class="bg-white border-b">
              <td class="ps-3 py-4">
                <div className="h-12 w-12 bg-black rounded-full"></div>
              </td>
              <td class="px-6 py-4">Israr</td>
              <td class="px-6 py-4 whitespace-break-spaces">Service Name</td>
              <td class="px-6 py-4 whitespace-nowrap">12-07-2023</td>
              <td class="px-6 py-4 whitespace-nowrap">12-07-2023</td>
              <td class="px-6 py-4 whitespace-nowrap">
                <button className="myCustomBtn px-3 py-1 rounded">
                  Allot work
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </main>
    </>
  );
}

export default AllotWork;
